import React, { useEffect,useState } from 'react';
import { Link} from 'react-router-dom';
import filterSummary from '../filterSummary';
import './details.css';
const ShowDetails = (props) => {
  const [data, setData] = useState({});
  const [loading, setLoading] = useState(false);
  const [castData, setcastData] = useState([]);
  let url = `https://api.tvmaze.com/shows/${props.match.params.id}`;
  let castUrl = `https://api.tvmaze.com/shows/${props.match.params.id}/cast`;
  const fetchData = async (url,castUrl) => {
    setLoading(true);
    
    let resp = await fetch(url);
    resp = await resp.json();
    setData(resp);

    let castResp = await fetch(castUrl);
    castResp = await castResp.json();
    // console.log("data", castResp);
    setcastData(castResp);

    setLoading(false);
  }

  useEffect(() => {
    fetchData(url,castUrl);
  }, []);
  const summary = data.summary ? filterSummary(data) : "summary not added yet";
  const image = data.image ? data.image.original : "https://static.tvmaze.com/images/no-img/no-img-portrait-text.png";
  const name = data.name;
  const genres = data.genres ? data.genres : [];
  const lang = data.language ? data.language : "Not added";
  return (
    <div className="">
      {/* {console.log(data)} */}
      <nav className="navbar navbar-expand-sm navbar-light bg-secondary">
        <div className="container">
          <p className="navbar-brand brand text-white">{data.name}</p>
        </div>
      </nav>
      {/* tabs starts */}
      <nav className="container mt-3">
        <div className="nav nav-tabs" id="nav-tab" role="tablist">
          <button className="nav-link active" id="nav-home-tab" data-bs-toggle="tab" data-bs-target="#nav-home" type="button" role="tab" aria-controls="nav-home" aria-selected="true">Main</button>
          <button className="nav-link" id="nav-profile-tab" data-bs-toggle="tab" data-bs-target="#nav-profile" type="button" role="tab" aria-controls="nav-profile" aria-selected="false">Cast</button>
          <button className="nav-link" id="nav-contact-tab" data-bs-toggle="tab" data-bs-target="#nav-contact" type="button" role="tab" aria-controls="nav-contact" aria-selected="false">Characters</button>
        </div>
      </nav>
      <div className="tab-content" id="nav-tabContent">
        {/* Main starts*/}
        <div className="tab-pane fade show active" id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">...
        <div className="container">
        {loading ? <h5>Loading...</h5> : ""}
        <div className="row mt-3">
          <div className="col-md-4 col-sm-12 col-xs-12 p-3">
            <div className="image_style d-flex">
              <img src={image} alt={name} style={{width:"30rem",border: "16px solid black"}} className="img-fluid"/>
            </div>
          </div>
            <div className="col-md-8 col-sm-12 col-xs-12 content_div">
            <div className="d-flex content_style row">
              <div className="col-md-6 col-sm-6 col-6">
              <h5>Genres </h5>
              </div>
              <div className="col-md-6 col-sm-6 col-6">
              <p> {
                genres.map((item)=>{return item? item+" ":""})
              }</p>
              </div>
            </div>
            <div className="d-flex content_style row">
               <div className="col-md-6 col-sm-6 col-6"> <h5>Language </h5></div>
                <div className="col-md-6 col-sm-6 col-6"><p>{ lang}</p></div>
            </div>
            <div className="d-flex content_style row">
               <div className="col-md-6 col-sm-6 col-6"><h5>Premiered </h5></div>
                <div className="col-md-6 col-sm-6 col-6">   <p>{data.premiered}</p></div>
            </div>
            <div className="d-flex content_style row">
               <div className="col-md-6 col-sm-6 col-6"> <h5>Status </h5></div>
                <div className="col-md-6 col-sm-6 col-6"><p>{data.status}</p></div> 
            </div>
            <div className="d-flex content_style row">
               <div className="col-md-6 col-sm-6 col-6"><h5>Official Site </h5></div>
                <div className="col-md-6 col-sm-6 col-6"><a href={data.officialSite} target="_blank">Official Site</a></div>
                </div>
                <p className="summ">{summary}</p>
            </div>
          </div>
        </div>
        </div>
        {/* Main ends*/}
        {/* cast start */}
        <div className="tab-pane fade container" id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab">
          <div className="row p-5">
          
            {castData.length!==0?castData.map((item, index) => {
              let image = item.person.image===null ?`https://static.tvmaze.com/images/no-img/no-img-portrait-text.png`: item.person.image.medium;
              return (
                <div className="col-lg-3 col-md-4 col-sm-6 col-12 d-flex justify-content-center" key={index}>
                  <div className="character_card">
                    <img src={image} className="img-fluid m-auto p-3" alt={item.character.name} key={index} />
                    <div className="m-auto character_info ps-1 pe-1">
                      <h5><span className="person">{item.person.name}</span> as <span className="character">{item.character.name}</span></h5>
                    </div>
                  </div>
                </div>
              );
            }):<h3>No information available</h3>
              }
          </div>
        </div>
        {/* cast start */}
        {/* character start */}
        <div className="tab-pane fade container" id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab">
          <div className="row p-5">
            {castData.length !== 0 ? castData.map((item, index) => {
            console.log(item)
            let image = item.character.image===null ? `https://static.tvmaze.com/images/no-img/no-img-portrait-text.png`:item.character.image.medium;
            return (
              <div className="col-lg-3 col-md-4 col-sm-6 col-12 d-flex justify-content-center" key={index}>
                <div className="character_card">
                  <img src={image} className="img-fluid m-auto p-3" alt={item.character.name} key={index} />
                  <div className="m-auto character_info ps-1 pe-1">
                    <h5><span className="person">{item.character.name}</span></h5>
                  </div>
                </div>
              </div>
            );
          }):<h3>No information available</h3>
            }
        </div>
        </div>
        {/* character start */} 
      </div>
      {/* tabs ends */}
      </div>
  );
}
export default ShowDetails;