const filterSummary = (data) => {
  let res,text;
  if (data.show) {
    res = data.show;
    const summary = res.summary ? res.summary : "";
    const regex = new RegExp('(<p>|</p>|<b>|</b>|&amp;|<i>|</i>|<br/>)', 'g');
    const summ = summary.replace(regex, "");
    text = summ.split(" ").splice(0, 10).join(" ");
  }
  else if (data._embedded) {
    res = data._embedded;
    const summary = res.summary ? res.summary : "";
    const regex = new RegExp('(<p>|</p>|<b>|</b>|<i>|</i>|<br/>|&amp;)', 'g');
    const summ = summary.replace(regex, "");
    text = summ.split(" ").splice(0, 1).join(" ");
  }
  else {
    res = data;
    const summary = res.summary ? res.summary : "";
    const regex = new RegExp('(<p>|</p>|<b>|</b>|<i>|</i>|<br/>|&amp;)', 'g');
    text = summary.replace(regex, "");
  }
  return (text);
}
export default filterSummary;